@import url(https://fonts.googleapis.com/css?family=Montserrat:400,800&display=swap);
html {
    font-size: 62.5%;
    box-sizing: border-box;
}

:root {
    --blue-primary: #66C3FF;
    --blue-secondary: #53D8FB;
    --white-primary: #DCE1E9;
    --grey-primary: #363732;
    --pink-primary: #D4AFB9;

    --xs-size: 0.6rem;
    --s-size: 0.8rem;
    --m-size: 1.6rem;
    --l-size: 2.4rem;
    --xl-size: 3.6rem;
    --xxl-size: 4.8rem;

    --s-breakpoint: 650px;
    --m-breakpoint: 800px;
    --xl-breakpoint: 1200px;
}

body {
    font-family: 'Montserrat', Helvetica, sans-serif;
    font-size: 1.6rem;
    font-size: var(--m-size);
    letter-spacing: 0.0625rem;
    line-height: 1.5;
    margin: auto;
    max-width: 1200px;
    max-width: var(--xl-breakpoint);
    position: relative;
}


*, *::after, *::before {
    box-sizing: inherit;
    padding: 0;
    margin: 0;
}

a {
    text-decoration: none;
}

button {
    background: #363732;
    background: var(--grey-primary);
    border: 1px solid #DCE1E9;
    border: 1px solid var(--white-primary);
    border-radius: 0.6rem;
    border-radius: var(--xs-size);
    color: #DCE1E9;
    color: var(--white-primary);
    cursor: pointer;
    padding: 0.8rem 0.6rem;
    padding: var(--s-size) var(--xs-size);
    outline: none;
    -webkit-transition: scale .2s ease-in-out;
    transition: scale .2s ease-in-out;
}

button:hover {
    border-color: #66C3FF;
    border-color: var(--blue-primary);
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
    color: #66C3FF;
    color: var(--blue-primary);
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
}

textarea {
    resize: vertical;
}
.card {
    /* If card heights are overlapping then increase card height */
    --card-height: 60rem;

    border-bottom: 1px solid rgba(54, 55, 50, 0.575);
    display: block;
    height: var(--card-height);
    margin: var(--s-size) 0;
    position: relative;
    overflow: hidden;
    /* change width to 50% then 30% at appropriate breakpoints */
    width: 100%;
    /* height: 100%; */
}

.card__title {
    font-size: var(--l-size);   
}

.card__img {
    /* Make sure img-height is less than card-height */
    --card__img-height: 50rem;

    border-radius: 5px;
    margin-bottom: var(--s-size);
    max-height: var(--card__img-height);
    width: auto;
}

.card__front {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    position: absolute;
    /* Pull to the right to hide scroll bar */
    /* Can safely be removed after 800px */
    padding-right: 21px;
    padding-bottom: var(--m-size);
    text-align: center;
    width: 100%;
}

.card--overflow {
    overflow-x: hidden;
    overflow-y: scroll;   
    right: -17px;
}

.card__title {
    font-size: var(--l-size);
    font-weight: normal;
}

.card__back {
    height: 100%;
    position: absolute;
    /* Pull to the right to hide scroll bar */
    /* Can safely be removed after 800px */
    padding-right: 17px;
    width: 100%;
}

.card__title--back {
    text-align: center;
}

.card__back-btns {
    padding: var(--s-size) 0;
    text-align: center;
}

.card__back-btn {
    margin: 0 var(--s-size);
}

.card__text {
    padding: var(--s-size) var(--xl-size);
    text-indent: var(--m-size);
}

.card-enter {
    opacity: 0.01;
}
  
.card-enter.card-enter-active {
    opacity: 1;
    -webkit-transition: opacity 500ms ease-in;
    transition: opacity 500ms ease-in;
}

.card-leave {
    opacity: 1;
}

.card-leave.card-leave-active {
    opacity: 0.01;
    -webkit-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
}


/* @media (min-width: var(--s-breakpoint) { */
@media (min-width: 650px) {
    .card {
        width: 50%;
    }
}

/* @media (min-width: var(--m-breakpoint) { */
@media (min-width: 800px) {
    .card {
        width: 32%;
    }
    .card--overflow {
        overflow-y: hidden;
        right: 0;
    }
    .card__front, .card__back {
        padding-right: 0;
    }
}
.cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
.next-icon {
    background: rgba(81, 85, 64, 0.397);
    color: var(--white-primary);
    font-size: var(--l-size);
    font-weight: 600;
    padding: var(--s-size);
    position: fixed;
    top: 60%;
    right: 0;
}
.animal-card-form {
    display: block;
    margin: var(--m-size) auto;
    position: relative;
}

.animal-form {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-row-gap: var(--m-size);
    padding: var(--m-size);
    position: absolute;
    width: 100%;
}

.animal-form-card {
    align-items: center;
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100%;
    margin: 0 auto;
}

.animal-form-card__btn {
    margin: var(--s-size);
}

.animal-form__input {
    padding: var(--m-size);
}

.animal-form-enter {
    opacity: 0.01;
}
  
.animal-form-enter.animal-form-enter-active {
    opacity: 1;
    -webkit-transition: opacity 500ms ease-in;
    transition: opacity 500ms ease-in;
}

.animal-form-leave {
    opacity: 1;
}

.animal-form-leave.animal-form-leave-active {
    opacity: 0.01;
    -webkit-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
}

/* @media (min-width: var(--s-breakpoint)) { */
@media (min-width: 650px) {
    .animal-card-form {
        width: 80%;
    }
}

/* @media (min-width: var(--m-breakpoint)) { */
@media (min-width: 800px) {
    .animal-card-form {
        width: 70%;
    }
}
.nav {
    align-items: center;
    background: var(--grey-primary);
    display: flex;
    flex-wrap: wrap;
    font-size: var(--l-size);
    justify-content: space-between;
}

.nav--fade {
    background: rgba(54, 55, 50, 0.575);
}

.nav__link {
    color: var(--white-primary);
    display: inline-block;
    text-decoration: none;
    padding: var(--s-size) var(--m-size);
}

.nav__link--slide {
    align-items: center;
    display: flex;
    flex-direction: column;
    /* height: 0; */
    width: 100%;
}

.nav__link--collapse {
    display: none;
    /* height: 100%; */
}

.nav__link:hover {
    color: var(--blue-primary);
}

.nav__link--active {
    color: var(--blue-primary);
}

.nav__link--white {
    color: var(--white-primary);
}

/* @media (min-width: var(--s-breakpoint)) { */
@media (min-width: 650px) {
    .nav__link--slide {
        width: auto;
    }
    .nav__icon--hide {
        display: none;
    }
    .nav__link--inline {
        display: inline;
    }
    .nav__link--collapse {
        display: block;
    }
}
.header {
    background: rgba(212, 175, 185, 0.472);
    position: relative;
    height: 100vh;
    width: 100%;
}

.header__img {
    max-height: 100vh;
    width: 100%;
    object-fit: cover;
    position: absolute;
    top: -5px;
    z-index: -1;
}

.header__box--center {
    align-items: center;
    color: var(--black-primary);
    display: flex;
    flex-direction: column;
    font-size: var(--l-size);
    position: absolute;
    justify-content: center;
    top: 35%;
    width: 100%;
}

.header__title {
    font-size: var(--xl-size);
    font-weight: normal;
}

.header__text {
    text-align: center;
}

.header__btn {
    margin: var(--s-size);
    font-size: var(--l-size);
}

.header__btn-box {
    margin-top: var(--m-size);
    text-align: center;
}
.home__nav {
    bottom: 0;
    position: absolute;
}

.sign-up-form {
    display: grid;
    font-size: var(--l-size);
    grid-row-gap: var(--m-size);
    margin: var(--l-size) auto;
    padding: var(--l-size);
}

.sign-up-form__input {
    padding: var(--m-size);
    font-size: var(--m-size);
}

.sign-up-form--red-bold {
    color: red;
    font-weight: 600;
}

.sign-up-form__btn {
    padding: var(--m-size) 0;
    font-size: var(--m-size);
}

/* @media (min-width: var(--s-breakpoint)) { */
@media (min-width: 650px) {
    .sign-up-form {
        width: 70%;
    }
}

/* @media (min-width: var(--m-breakpoint)) { */
@media (min-width: 800px) {
    .sign-up-form {
        width: 50%;
    }
}

